<template>
  <div>
    <div class="ant-col-xs-6 ant-col-sm-6 ant-col-md-6 ant-col-lg-1 ant-col-xl-1 ant-col-xxl-1">
      <div class="sidebar__logo">
        <router-link :to="{ name: 'dashboard'}">
          <img
            alt="logo"
            height="32"
            src="data:image/svg+xml;base64,"
          >
        </router-link>
      </div>
    </div>
    <div class="ant-col-md-18 ant-col-lg-23 ant-col-xl-23 ant-col-xxl-23">
      <a-menu mode="horizontal" id="header" :style="{height: '49px' }">
        <a-dropdown >
          <a class="ant-dropdown-link" href="#">
            <span slot="title" class="submenu-title-wrapper">
              <a-avatar size="large" :src="user.avatar"/>
            </span>
          </a>
          <a-menu slot="overlay">
            <a-menu-item key="0" disabled>
             {{ user.name }}
            </a-menu-item>
            <a-menu-item key="1">
              <a-menu-item key="3"><a-icon type="user" /> Account Settings</a-menu-item>
            </a-menu-item>
            <a-menu-item key="2">
              <a-menu-item key="3"><a-icon type="key" /> API keys</a-menu-item>
            </a-menu-item>
            <a-menu-divider/>
            <a-menu-item key="3"><a-icon type="logout" /> Logout</a-menu-item>
          </a-menu>
        </a-dropdown>
      </a-menu>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["user", "isLoggedIn"])
  },
  watch: {
    isLoggedIn(val) {
      if (!val)
        this.$router.push({
          name: "login",
          query: { r: this.$route.fullPath }
        });
    }
  },
  methods: mapActions(["logout"])
};
</script>

<style>
#header {
  float: right;
  border: none;
  margin-top: 15px;
}
</style>